<template>
  <v-card class="overflow-hidden">
    <v-app-bar
      absolute
      color="white"
      elevate-on-scroll
      scroll-target="#scrolling-techniques-7"
    >
      <v-toolbar-title>Home</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-heart</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>
    </v-app-bar>
    <v-sheet
      id="scrolling-techniques-7"
      class="overflow-y-auto content-body-container"
      max-height="600"
    >
      <v-container style="height: 1500px">
        <div
          v-if="postImage"
          class="imagePreviewWrapper"
          :style="{
            'background-image': `url(${previewImage})`,
          }"
        ></div>
        <v-textarea
          v-model="postText"
          no-resize
          filled
          auto-grow
          placeholder="What's happening?"
        ></v-textarea>
        <v-row class="d-flex" justify="space-between" align="center">
          <v-col
            ><v-file-input
              ref="fileInput"
              :rules="rules"
              accept="image/png, image/jpeg, image/bmp"
              label="File input"
              filled
              hide-input
              v-model="postImage"
              prepend-icon="mdi-camera"
              @change="pickFile"
            ></v-file-input
          ></v-col>
          <v-col
            ><v-btn color="blue" class="white--text mb-3" @click="onSubmitPost"
              >Post</v-btn
            ></v-col
          >
        </v-row>

        <v-divider></v-divider>
        <PostList v-if="posts" :posts="posts" />
      </v-container>
    </v-sheet>
  </v-card>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import PostList from "@/components/PostList";
export default {
  components: {
    PostList,
  },
  data: () => ({
    rules: [(value) => value != null],
    postText: "",
    postImage: null,
    postType: "",
    previewImage: null,
  }),
  methods: {
    ...mapActions(["getPosts", "createPost"]),
    pickFile(file) {
      if (file) {
        let reader = new FileReader();
        reader.onload = (e) => {
          this.previewImage = e.target.result;
          this.postType = file.type;
        };
        reader.readAsDataURL(file);
      }
    },
    onSubmitPost() {
      if (this.postText != "" && this.postText.trim() != "") {
        let fd = new FormData();
        if (this.postImage) {
          fd.append("postImage", this.postImage);
          fd.append("type", this.postType);
        } else {
          fd.append("type", "text");
        }
        fd.append("creator", localStorage.getItem("userId"));
        fd.append("text", this.postText);
        this.createPost(fd).then((createdPost) => {
          this.getPosts();
          this.postText = "";
          this.postType = "";
          this.postImage = null;
        });
      }
    },
  },
  computed: {
    ...mapGetters(["posts"]),
  },
  created() {
    this.getPosts();
  },
};
</script>

<style lang="scss" scoped>
.imagePreviewWrapper {
  width: 250px;
  height: 250px;
  display: block;
  cursor: pointer;
  margin: 0 auto 30px;
  background-size: cover;
  background-position: center center;
}
</style>
